<template>
<div class="activity">
    <HeaderBar></HeaderBar>
    <div class="topbar">
        <h4 class="pagetitle">
            活動管理
        </h4>
        <div class="searchwrap">
            <SearchBar @emitkeyword="keywordsearch"></SearchBar>
        </div>

        <div class="addactivity">
            <i class="ri-calendar-event-fill"></i>
            <span>新增活動</span>
        </div>
    </div>
    <div class="content">
        <div class="lefttab">
            <div v-for="(item,index) in itemlist" :key="index">
                <Tabitem :tabname="item.name" :isall=isnotall :isclick="index==tabcurrentindex" @emittabitem=clicktabitem(index,item.name)></Tabitem>
            </div>

        </div>
        <div class="activitylist">
            <div class="activitytopbar">
                <div class="totalnumber">
                    <span>1-15場活動（總共60場）</span>
                </div>
                <div class="dropdownwrap">
                    <Dropdown :catlist="catlist" @itemhandler="activitycata"></Dropdown>
                </div>
                <div class="timefilterwrap">
                    <Timefilter></Timefilter>
                </div>
            </div>
            <div v-for="(act,index) in filterlist" :key="index">
                <a @click="toactmember">
                    <Activitylist @emitdeleact="deleact(index)" :actname="act.name" :actpos="act.pos" :acttiime="act.time" :acttag="act.tag" :actin="act.actin"></Activitylist>
                </a>
            </div>

        </div>

    </div>
    <Deleactpopup :name="actlist[deleactindex].name" :time="actlist[deleactindex].time" :actin="actlist[deleactindex].actin" v-if="isdele" @emitactdele="deleacthandler"></Deleactpopup>
</div>
</template>

<script>
// @ is an alias to /src
import HeaderBar from '../components/Header'
import SearchBar from '../components/Searchbar'
import Tabitem from '../components/Tabitem'
import Dropdown from '../components/Dropdown'
import Timefilter from '../components/Timefilter'
import Activitylist from '../components/Activitylist'
import Deleactpopup from '../components/Deleactpopup'
export default {
    data() {
        return {
            catlist: ['全部', '親子', 'DIY', '戶外', '食物'],
            itemlist: [{
                name: '所有場館',
                isclick: true
            }, {
                name: '會館一',
                isclick: false
            }, {
                name: '會館二',
                isclick: false
            }, {
                name: '會館三',
                isclick: false
            }],
            actlist: [{
                    name: '帆布袋蝶古巴特',
                    pos: '會館一',
                    time: '2/19(五) 中午12:00 - 下午2:00',
                    tag: ['親子', '手做'],
                    cata: 'DIY',
                    actin: '10'

                },
                {
                    name: '親子手做提拉米蘇',
                    pos: '會館二',
                    time: '4/19(二) 中午12:00 - 下午2:00',
                    tag: ['親子', '手做', '甜點'],
                    cata: '親子',
                    actin: '5'

                },
                {
                    name: '青銀共餐一起來',
                    pos: '會館三',
                    time: '5/30(一) 中午12:00 - 下午2:00',
                    tag: ['手作活動', '只限男性', '30以上'],
                    cata: '食物',
                    actin: '10'

                }
            ],
            isall: true,
            isnotall: false,
            currentcata: '全部',
            currentstadium: '所有場館',
            tabcurrentindex: 0,
            keyword: '',
            isdele: false,
            deleactindex: 0
        }
    },
    computed: {
        filterlist() {
            return this.actlist.filter(item => {
                if (this.currentcata == "全部" && this.currentstadium == "所有場館" && item.name.search(this.keyword) == 0) {
                    if (item.name.search(this.keyword) != -1) {
                        return item
                    }
                    return this.actlist
                } else {
                    if (this.currentcata == "全部") {
                        if (item.pos == this.currentstadium && item.name.search(this.keyword) != -1) {
                            return item
                        }
                    } else if (this.currentstadium == "所有場館") {
                        if (item.cata == this.currentcata && item.name.search(this.keyword) != -1) {
                            return item
                        }
                    } else if (item.pos == this.currentstadium && item.cata == this.currentcata && item.name.search(this.keyword) != -1) {
                        return item
                    }

                }

            })
        },
    },
    methods: {
        clicktabitem(val, name) {
            this.currentstadium = name;
            this.tabcurrentindex = val;
        },
        activitycata(val) {
            this.currentcata = val

            console.log('有點到', val);
        },
        keywordsearch(val) {
            this.keyword = val;
            console.log(val);
        },
        deleact(index) {
            this.deleactindex = index;
            this.isdele = true;
            // this.actlist.splice(index,1)
            console.log(index)
        },
        deleacthandler(type) {
            console.log(type)
            if (type == "取消") {
                this.isdele = false
            } else if (type == "刪除") {
                this.isdele = false
                this.actlist.splice(this.deleactindex, 1)
            }

        },
        toactmember(){
          this.$router.push({ name: 'ActivityMemberManage'})  
        }
    },
    components: {
        HeaderBar,
        SearchBar,
        Tabitem,
        Dropdown,
        Timefilter,
        Activitylist,
        Deleactpopup
    }
}
</script>

